@import url(https://fonts.googleapis.com/css?family=Roboto);
@import url(https://fonts.googleapis.com/css?family=Oleo+Script);
* {
  margin: 0;
  padding: 0;
  font-family: "Roboto",-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
ul{
  list-style-type: none;
}
.App {
  text-align: center;
  overflow-x: hidden; }

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  border-radius: 5px;
  background-color: #F5F5F5; }

::-webkit-scrollbar {
  width: 5px;
  background-color: #F5F5F5; }

::-webkit-scrollbar-thumb {
  border-radius: 5px;
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #555; }

.slide-fwd-center > div:hover img {
  -webkit-animation: rotate-in-center 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
  animation: rotate-in-center 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94) both; }

@-webkit-keyframes rotate-in-center {
  0% {
    -webkit-transform: rotate(-360deg);
    transform: rotate(-360deg);
    opacity: 0; }
  100% {
    -webkit-transform: rotate(0);
    transform: rotate(0);
    opacity: 1; } }

@keyframes rotate-in-center {
  0% {
    -webkit-transform: rotate(-360deg);
    transform: rotate(-360deg);
    opacity: 0; }
  100% {
    -webkit-transform: rotate(0);
    transform: rotate(0);
    opacity: 1; } }

.col,
.col-md-12 {
  padding: 0px; }

.container-fluid {
  padding: 0px; }

.goUp {
  width: 56px;
  height: 56px;
  background-color: #ffffff;
  position: fixed;
  bottom: 2%;
  right: 2%;
  cursor: pointer;
  border-radius: 30px;
  line-height: 50px; }

.goUp:hover {
  box-shadow: 0 3px 3px 0 rgba(0, 0, 0, 0.14), 0 1px 7px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -1px rgba(0, 0, 0, 0.2);
  opacity: 0.75;
  -webkit-transition: .3s;
  transition: .3s; }

.section1 h1 {
  color: #5d7283;
  text-shadow: 2px 2px #000000;
  font-family: 'Oleo Script', cursive;
  font-size: 3.6rem;
  font-weight: 400; }

.section1 h2 {
  color: #9ca9ad;
  font-size: 2.5rem;
  text-shadow: 2px 2px #000000;
  font-family: 'Oleo Script', cursive; }

.section1 h5 {
  color: #5d7283;
  font-size: 1.64rem;
  text-shadow: 1px 1px #0a0b0b;
  font-family: 'Oleo Script', cursive; }

.section1 > div {
  min-height: 500px;
  padding: 50px 0px; }

.headerIcons {
  display: inline-block;
  margin-bottom: 0px; }
  .headerIcons li {
    margin: 5px !important;
    float: left; }
    .headerIcons li a {
      width: 45px;
      height: 45px;
      border: 2px solid #fff;
      border-radius: 100%;
      color: #fff;
      font-weight: bold;
      line-height: 40px;
      transition: .5s ease-in-out;
      -webkit-transition: .5s ease-in-out;
      -moz-transition: .5s ease-in-out;
      -o-transition: .5s ease-in-out;
      -ms-transition: .5s ease-in-out;
      opacity: 0.6;
      font-size: 0px; }
    .headerIcons li a:before {
      font-size: 20px; }
    .headerIcons li a:hover {
      color: #fff;
      line-height: 40px;
      border: 2px solid #F6EDD3;
      opacity: 1; }
    .headerIcons li .fa-linkedin {
      background: #0277bd; }
    .headerIcons li .fa-facebook {
      background: #039be5; }
    .headerIcons li .fa-code-fork {
      background: #fc792a; }
    .headerIcons li .fa-phone {
      background: #4CAF50; }
    .headerIcons li .fa-envelope-o {
      background: #F44336; }

.files a {
  background: #7289a5;
  border-radius: 5px;
  transition: .35s ease-in-out;
  -webkit-transition: .35s ease-in-out;
  -moz-transition: .35s ease-in-out;
  -o-transition: .35s ease-in-out;
  -ms-transition: .35s ease-in-out;
  cursor: pointer; }

.files a:hover {
  background-color: #fff; }

/*section 2*/
.scrollDown {
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  margin: 0.5rem 0 1rem 0; }
  .scrollDown a {
    color: #5f738a;
    padding: 14px;
    display: block; }

.section2 {
  min-height: 355px;
  margin-top: 30px; }
  .section2 img {
    margin: 5% 0%; }
  .section2 h5 {
    font-weight: bold;
    font-family: 'Oleo Script', cursive; }

/*section 3*/
.section3 {
  min-height: 600px;
  overflow: hidden; }
  .section3 .sentence {
    color: #fef8ed;
    text-shadow: 1px 1px #000000;
    width: 70%;
    text-align: center;
    font-size: 2rem;
    font-family: 'Oleo Script', cursive;
    margin: 10% auto; }
  .section3 .iphoneWrapper {
    position: relative;
    width: 345px;
    margin: 0 auto; }
    .section3 .iphoneWrapper .rightCarousel,
    .section3 .iphoneWrapper .leftCarousel {
      position: absolute;
      top: 45%;
      color: #fff; }
    .section3 .iphoneWrapper .leftCarousel {
      left: 0%; }
    .section3 .iphoneWrapper .iphone {
      position: relative;
      height: 600px;
      background-size: contain;
      margin-top: 30px;
      width: 324px !important;
      overflow: hidden;
      margin: 0 auto; }
    .section3 .iphoneWrapper .carousel {
      position: absolute;
      top: 80px;
      left: 0;
      height: 409px;
      width: 244px;
      right: 0;
      margin: 0 auto; }
    .section3 .iphoneWrapper .carousel-item .view {
      opacity: 0.85; }
    .section3 .iphoneWrapper .carousel-item .view:hover {
      opacity: 1;
      -webkit-transition: .3s;
      transition: .3s; }
    .section3 .iphoneWrapper .carousel-caption {
      left: 0;
      background: #5a5a5aba;
      font-weight: bold;
      width: 100%; }
    .section3 .iphoneWrapper .carousel-control-prev-icon,
    .section3 .iphoneWrapper .carousel-control-next-icon {
      position: absolute;
      width: 30px;
      height: 30px;
      left: -50px; }
    .section3 .iphoneWrapper .carousel-control-next-icon {
      left: 55px; }
    .section3 .iphoneWrapper .carousel-fade .carousel-item {
      transition: .5s ease-in-out;
      -webkit-transition: .5s ease-in-out;
      -moz-transition: .5s ease-in-out;
      -o-transition: .5s ease-in-out;
      -ms-transition: .5s ease-in-out; }

/*section 4*/
.imagination {
  box-shadow: 0 8px 17px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  background: #0A1827;
  color: #fff;
  font-size: 30px;
  width: 70%;
  margin: 0 auto;
  -webkit-transition: -webkit-box-shadow .25s;
  -webkit-transition: box-shadow .25s;
  transition: box-shadow .25s;
  padding: 24px;
  margin: 2% auto;
  border-radius: 2px; }
  .imagination span {
    font-family: 'Oleo Script', cursive; }
  .imagination .orange-text.text-lighten-2 {
    color: #F6EDD3 !important; }

/*section 5*/
#contact {
  color: #fff;
  padding: 2% 10%;
  background-color: #0a1827; }
  #contact h4 {
    font-family: 'Oleo Script', cursive;
    text-align: left; }
  #contact p {
    font-family: 'Roboto', sans-serif;
    color: #fff;
    text-align: left;
    font-weight: normal; }
  #contact .md-form .prefix {
    left: 0; }
  #contact .md-form .prefix.active,
  #contact .md-form input:focus + label {
    color: #39bde8; }
  #contact .md-form input:focus {
    box-shadow: 0 1px 0 0 #39bde8;
    border-bottom: 1px solid #39bde8;
    color: #fff; }
  #contact .btn-info {
    float: left; }
  #contact .btn-floating {
    color: #fff;
    position: relative;
    overflow: hidden;
    z-index: 1;
    width: 40px;
    height: 40px;
    line-height: 40px;
    margin-left: 0px;
    float: left;
    padding: 0;
    background-color: #26a69a;
    border-radius: 50%;
    -webkit-transition: .3s;
    transition: .3s;
    cursor: pointer;
    vertical-align: middle; }
  #contact .btn-floating:hover,
  #contact .btn-info:hover {
    box-shadow: 0 3px 3px 0 rgba(0, 0, 0, 0.14), 0 1px 7px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -1px rgba(0, 0, 0, 0.2);
    opacity: 0.75;
    -webkit-transition: .3s;
    transition: .3s; }
  #contact .linkedin {
    background-color: #0277bd !important; }
  #contact .facebook {
    background-color: #039be5 !important; }
  #contact .phone {
    background-color: #4CAF50 !important; }
  #contact .mail {
    background-color: #F44336 !important; }

@media only screen and (max-width: 767px) {
  .imagination {
    width: 100%; }
  #contact .btn-floating {
    margin: 0px 10px; } }

